<template>
	<div>
		<component
			v-bind:is="columnContent"
			:fieldValue="params.value"
			:fieldSettings="params.settings"
			:mode="mode"
			:view="view"
			@onChange="changeValue"
			@openEdit="openEdit"
		></component>
	</div>
</template>
<script>
	import Vue from 'vue';
	export default
	{
		props: ['params','mode','view','fieldName'],
		computed:
		{
			isCustom()
			{
				return this.params.settings.type == 'custom';
			},
			/**
			 * Динамически отображаемая ячейка таблицы
			 */
			columnContent()
			{
				if(this.params.settings.type == 'custom')
				{
					// return new Vue({render (h) {return h('div', this.hi) } });
					// return Vue.component('terms-of-service', {template: ` <div v-once> <h1>Условия использования</h1> ... много-много статического контента ... </div> ` });
					// return false;
					// debugger;
					return eval(this.params.settings.fieldJs)
				}
				if (typeof this.fieldName == 'undefined' ||  this.fieldName === false )
					return false;

				return () => import(`@/components/fields/${this.fieldName}/Field.vue`);
			}
		},
		methods:
		{
			/**
			 * Отслеживать изменения в филде
			 * @value {
			 *        value
			 *        settings (main format)
			 * }
			 */
			changeValue(value)
			{
				this.$emit('onChange', value);
			},

			/**
			 * Opens edit page
			 */
			openEdit()
			{
				this.$emit('openEdit');
			}
		}
	}
</script>